
//header========================
.header {
    padding-top: 15px;
    @media (max-width: 465px) {
        padding-top: 0;
    }

    &__top-menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 15px;
        position: relative;
        @media (max-width: 980px) {
            height: 120px;
            align-items: start;
        }
        @media (max-width: 465px) {
            height: 80px;
            padding-bottom: 0;
        }
        @media (max-width: 392px) {
            padding: 10px 0;
            height: 100px;
        }
    }
    &__logo {
        width: 225px;
        height: 70px;
        display: block;
        @media (max-width: 678px) {
            width: 200px;
            height: 65px;
        }
        @media (max-width: 465px) {
            display: none;
        }
        img {
            max-width: 100%;
            height: 100%;
        }
    }
    &__location {
        font-size: 16px;
        font-weight: 700;
        color: #333333;
    }
    &__phone {
        font-size: 16px;
        font-weight: 700;
        color: #333333;
        transition: transform .3s;
        display: flex;
        align-items: center;
        @media (max-width: 678px) {
            font-weight: 500;
        }
        &:hover {
            transform: translateY(-2px);
        }
        img {
            margin-right: 3px;
            margin-top: 5px;
        }
    }
    &__btn-wrp {
        @media (max-width: 465px) {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        @media (max-width: 392px) {
            flex-direction: column;
        }
    }
    &__top-btn {
        width: 180px;
        height: 60px;
        background-color: transparent;
        border: 1px solid #096FA9;
        color: #096FA9;
        border-radius: 5px;
        transition: background .3s;
        font-size: 16px;
        font-weight: 500;
        &:hover {
            background-color: #096FA9;
            color: #fff;
        }
        @media (max-width: 980px) {
            margin-top: 10px;
        }
        @media (max-width: 678px) {
            height: 50px;
        }
        @media (max-width: 465px) {
            margin-top: 0;
        }
        @media (max-width: 392px) {
            width: 100%;
        }
    }
    &__bottom-menu {
        padding: 15px 0;
        height: 100px;
        display: flex;
        align-items: center;
        box-shadow: 0 0 4px 0 rgb(0 0 0 / 10%);
        position: relative;
        @media (max-width: 685px) {
            height: 60px;
        }
        &.active {
            z-index: 3;
            top: 0;
            left: 0;
            background-color: rgba(255, 255, 255, 0.95);
            position: fixed;
            width: 100%;
            height: 70px;
            @media (max-width: 685px) {
                height: 60px;
            }
        }
    }
}
.loc-decst {
    @media (max-width: 980px) {
        display: none;
    }
}
.loc-mob {
    display: none;
    width: 245px;
    margin-top: 5px;
    &.active {
        display: none;
    }
    @media (max-width: 980px) {
        display: block;
    }
    @media (max-width: 678px) {
        font-size: 14px;
        font-weight: 500;
    }
}
.number-decst {
    @media (max-width: 980px) {
        display: none;
    }
}
.number-mob {
    display: none;
    @media (max-width: 980px) {
        display: flex;
        margin-top: 5px;
    }
}


