
.burger {
    position: absolute;
    top: 20px;
    right: 15px;
    z-index: 4;
    width: 30px;
    height: 30px;
    display: none;
    cursor: pointer;
    @media (max-width: 685px) {
        display: block;
    }
    span {
        width: 30px;
        height: 2px;
        background-color: #000;
        position: absolute;
        pointer-events: none;
        transition: all .3s;
        &:nth-child(1) {
            &.active {
                transform: rotate(-45deg);
                top: 7px;
            }
        }
        &:nth-child(2) {
            top: 7px;
            &.active {
                //display: none;
                opacity: 0;
                transform: translateX(-50px);
            }
        }
        &:nth-child(3) {
            top: 14px;
            &.active {
                top: 7px;
                transform: rotate(45deg);
            }
        }
    }
}


.menu {
    width: 1230px;
    margin: 0 auto;
    display: block;
    padding: 15px;
    position: relative;
    transition: transform .3s;

    @media (max-width: 685px) {
        overflow: auto;
        position: fixed;
        right: 0;
        top: 0;
        z-index: 3;
        width: 400px;
        background-color: #fff;
        padding: 20px 15px 20px 15px;
        transform: translateX(100%);
        height: 100vh;
        &.active {
            transform: translateX(0);
        }
        &:first-child {
            &.active {
                justify-content: end;
            }
        }
        @media (max-width: 400px) {
            width: 100%;
        }
    }
    // &.active {
    //     width: 100%;
    // }
    &__list {
        display: flex;
        transition: all .3s;

        @media (max-width: 685px) {
            display: block;
            min-height: 100%;
            padding: 20px 0;
        }
    }
    &__item {
        &:not(:last-child) {
            margin-right: 30px;
            @media (max-width: 685px) {
                margin-bottom: 30px;
            }
        }
        a {
            font-size: 16px;
            font-weight: 500;
            color: #333333;
            transition: color .3s;
            &:hover {
                color: rgb(7, 133, 178);
            }
        }
    }
}

body {
    &.active {
        overflow: hidden;
    }
}