
.modal-body {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.631);
    display: flex;
    justify-content: center;
    padding: 100px 0px 70px 0px;
    z-index: 5;
    transition: .5s;
    overflow: auto;
    @media (max-width: 522px) {
        padding-left: 15px;
        padding-right: 15px;
    }
}
._body-call, ._body-consult {
    opacity: 0;
    visibility: hidden;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.form {
    width: 500px;
    height: 500px;
    background-color: #fff;
    position: relative;
    padding: 100px 15px 30px 15px;
    transform: perspective(600px) translateY(-100%) rotateX(45deg);
    transition: transform 0.8s ease 0s;
    &.active {
        transform: translateY(0);
    }

    input {
        display: block;
        width: 100%;
        height: 60px;
        padding: 0 10px;
    }
    .input {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }
    &__title {
        text-align: center;
        font-size: 32px;
        font-weight: 700;
        padding-bottom: 30px;
        margin-bottom: 30px;
        border-bottom: 1px solid #898989;
        @media (max-width: 488px) {
            font-size: 25px;
        }
        @media (max-width: 392px) {
            font-size: 20px;
        }
    }
    &__name, &__phone, &__email, &__area-consult {
        background-color: rgb(240, 248, 255);
    }
    &__name, &__email, &__area-consult {
        &::-webkit-input-placeholder {
            font-size: 16px;
            font-weight: 600;
            color: #898989;
        }
        &::-moz-placeholder          {
            font-size: 16px;
            font-weight: 600;
            color: #898989;
        }/* Firefox 19+ */
        &:-moz-placeholder           {
            font-size: 16px;
            font-weight: 600;
            color: #898989;
        }/* Firefox 18- */
        &:-ms-input-placeholder      {
            font-size: 16px;
            font-weight: 600;
            color: #898989;
        }
    }
    &__phone {

    }
    &__btn {
        width: 100%;
        height: 60px;
        background-color: #096FA9;
        font-size: 18px;
        color: #fff;
        font-weight: 700;
        margin-top: 30px;
        display: block;
    }
    &__cross-call, &__cross-consult {
        position: absolute;
        right: 9px;
        top: 0;
        font-size: 50px;
        //font-weight: 500;
        cursor: pointer;
    }
    &.call-form {
        @media (max-width: 522px) {
            height: 427px;
        }
    }

    &.consult-form {
        padding: 30px 15px 30px 15px;
        height: 620px;
    }
    &__area-consult {
        width: 100%;
        height: 100px;
        resize: none;
        padding: 20px 10px 10px 10px;
   }
   @media (max-width: 522px) {
        width: 100%;
        padding-top: 55px;
   }
}

