//main==============================
.main {
    width: 100%;
    min-height: 470px;
    position: relative;
    z-index: 1;

    &__content-wrp {
        background-image: url(../img/main/main_bg.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 0 0;
    }
    &__content-left {
        width: 470px;
        height: 470px;
        background-color: rgba(51, 122, 183, 0.85);
        padding: 100px 40px 70px 40px;
        line-height: 1.2;
        @media (max-width: 499px) {
            width: 100%;
            padding-left: 15px;
        }
    }
    &__title {
        font-size: 32px;
        font-weight: 700;
        color: #fff;
        @media (max-width: 430px) {
            font-size: 25px;
        }
        @media (max-width: 375px) {
            font-size: 20px;
        }
    }
    &__theses {
        margin-top: 20px;
        &:after {
            content: '';
            width: 100%;
            height: 1px;
            background-color: #fff;
            display: block;
            margin-top: 30px;
        }
    }
    &__theses-item {
        display: flex;
        &:not(:last-child) {
            margin-bottom: 7px;
        }
        span {
            color: #fff;
            font-size: 16px;
            margin-top: 2px;
        }
        img {
            min-width: 20px;
            height: 20px;
            display: block;
            margin-right: 5px;
        }
    }
    &__btn {
        width: 230px;
        height: 40px;
        background-color: transparent;
        border: 1px solid #fff;
        margin-top: 30px;
        color: #fff;
        border-radius: 5px;
        transition: background .3s;
        font-size: 16px;
        font-weight: 500;
        &:hover {
            background-color: #fff;
            color: #096FA9;
        }
    }
}
//конец main==============================