//theses==================================
.theses {
    width: 100%;
    position: relative;
    min-height: 600px;
    background-image: url(../img/theses/theses_bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    bottom: 260px;
    padding-top: 320px;
    padding-bottom: 50px;
    @media (max-width: 825px) {
        padding-top: 282px;
        padding-bottom: 35px;
    }

    &__cards {
        display: flex;
        justify-content: center;
        column-gap: 55px;
        @media (max-width: 825px) {
            column-gap: 20px;

            flex-wrap: wrap;
        }
    }
    &__card-item {
        position: relative;
        height: 195px;
        width: 270px;
        padding: 40px 30px;
        margin-top: 15px;
        @media (max-width: 825px) {
            background-color: #fff;
            border-radius: 5px;
            -webkit-box-shadow: 1px 1px 9px 3px rgba(71,71,71,0.13);
            box-shadow: 1px 1px 9px 3px rgba(71,71,71,0.13);
        }
    }
    &__border {
        position: absolute;
        top: 0;
        left: 0;
        @media (max-width: 825px) {
            display: none;
        }
    }
    &__icon {
        @media (max-width: 825px) {
            display: block;
            margin: 0 auto;
        }
    }
    p {
        margin-top: 20px;
        line-height: 1.5;
        font-size: 14px;
        font-weight: 700;
        @media (max-width: 825px) {
            text-align: center;
        }
        span {
            color: #096FA9;
        }
    }
}

//конец theses==================================